    selectItem(isBatchSearch) {
      let _this = this
      if(isBatchSearch && _this.searchBatchId != ""){
        let thisIndex = 0
        _this.colTimeList.forEach(function(item){
          if(item.batchId == _this.searchBatchId){
            thisIndex = item.index
            _this.tableScrollMove('tableLeft',thisIndex)
            return false
          }
        })
        // _this.getTable(_this.currentPage,_this.currentNum,_this.searchBatchId)
        _this.$refs.tableLeft.setCurrentRow(_this.colTimeList[thisIndex],true)
      }else{
        _this.getTable(_this.currentPage,_this.currentNum,_this.currentColTime.batchId)
      }
      
