    // 打包弹窗里点击确定
    SavePackage(formName) {
      this.$refs[formName].validate((valid) => {
          if (valid) {
            let itemInfoArr = []
            if(this.selectData){
              
              this.selectData.forEach(function(item){
                let itemInfoObj = {
                itemId:"",
                auditPlanId:""
              }
                itemInfoObj.itemId = item.itemId
                itemInfoObj.auditPlanId = item.auditPlanId
                itemInfoArr.push(itemInfoObj)
              })
            }
            let packParam = {
              batchId:this.currentColTime.batchId,         
              batchGroupName:this.form.batchGroupName,    
              auditType:this.form.auditType,       
              auditStartDate:this.form.auditStartDate,    
              auditFinishDate:this.form.auditFinishDate,  
              itemInfo:itemInfoArr
            }
            this.packageLoading = true
            batchPackage(packParam).then(res => {
              this.packageLoading = false
              if(res.data.successful){
                this.dialogPackagingVisible = false
                this.$refs.singleTable.clearSelection()
                this.getTable(this.currentPage,this.currentNum,this.currentColTime.batchId)
              }
            })

          } else {
            return false;
          }
        });
      
    },
    // 打开打包弹窗
    showDialogPackaging(){
      if(this.selectData == "" || this.selectData == null || this.selectData == undefined){
        this.$message({
          showClose: true,
          message: "请选择批次",
          type: 'warning'
        });
        return false
      }else{
        for(let i=0; i<this.selectData.length; i++){
          if(this.selectData[i].auditPlanStatus == '已通过' || this.selectData[i].auditPlanStatus == '已提交'){
            this.$message({
              showClose: true,
              message: "所选项目中不能包含已通过或者已提交状态的项目，请重新选择",
              type: 'warning'
            });
            return false
          }
          if(this.selectData[i].isBatchPackage ==1){
            this.$message({
              showClose: true,
              message: "所选项目中不能包含批次包，请重新选择",
              type: 'warning'
            });
            return false
          }
        }
      }
      this.dialogPackagingVisible = true
    },
