    // 点击编辑计划弹窗里面的确定
    editPlan(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.planParam.auditType = this.planForm.auditType
          this.planParam.planStartDate = this.planForm.planStartDate
          this.planParam.planEndDate = this.planForm.planEndDate
          editAuditPlan(this.planParam).then(res => {
            if(res.data.successful){
              this.dialogPlanVisible = false
              this.$refs.singleTable.clearSelection()
              this.getTable(this.currentPage,this.currentNum,this.currentColTime.batchId)
              // this.ToClose()
            }
          })
        }
      })
      
    },
    // 点击编辑评审计划打开弹窗
    showDialogPlan(row) {
      this.planForm.batchGroupName = row.itemName
      this.planForm.planStartDate = row.planStartDate
      this.planForm.planEndDate = row.planEndDate
      this.planForm.auditType = row.auditType
      this.planParam.itemId = row.itemId
      this.planParam.auditPlanId = row.auditPlanId
      if(row.isBatchPackage ==1){
        this.planParam.isBatchPackage = true
      }else{
        this.planParam.isBatchPackage = false
      }
      this.dialogPlanVisible = true
    },
