    //移出包
    moveOutPackage() {
        let _this = this
        
        let shiftOutArr =[]
        if (_this.packageBatchParam.childItemList.length == 0) {
            this.$message({
                showClose: true,
                message: '请选择要移出的项目',
                type: 'warning'
            });
            return false
        }
        
        let childArr =_this.packageBatchParam.childItemList
        childArr.forEach(function(item){
          let shiftOutItemParam = {
            itemId: "",
            hasChildren:""
          }
          shiftOutItemParam.itemId = item.substring(0, item.length-1)
          shiftOutItemParam.hasChildren = item.substr(item.length-1)
          shiftOutArr.push(shiftOutItemParam)
        })
        debugger
        shiftOutBatchPackage(shiftOutArr).then(response => {
            if (response.data.successful) {
                this.$message.success("移出项目包成功")
                _this.packageBatchParam.childItemList = []
                _this.showBatchPackage()
            } else {
                this.$message.error("移出项目包失败")
            }
        })
    },
    //移入项目包
    moveInPackage() {
        let _this = this
        if (_this.packageBatchParam.parentId == '') {
            this.$message({
                showClose: true,
                message: '请选择要移入的批次包',
                type: 'warning'
            })
            return false
        }
        if (_this.moveInPackageArr.length == 0) {
            this.$message({
                showClose: true,
                message: '请选择要移入的项目',
                type: 'warning'
            })
            return false
        }
        let moveInArr = []
        debugger
        _this.moveInPackageArr.forEach(function(item){
          let moveInPackageParams = {}
          moveInPackageParams.itemId = item.itemId
          moveInPackageParams.hasChildren = item.hasChildren
          moveInPackageParams.batchGroupId = _this.packageBatchParam.parentId
          moveInArr.push(moveInPackageParams)
        })
        shiftInBatchPackage(moveInArr).then(response => {
            if (response.data.successful) {
                _this.$refs.batchPackage.clearSelection();
                _this.moveInPackageArr = [];
                _this.$message.success("移入项目包成功")
                _this.showBatchPackage()
            } else {
                this.$message.error("移入项目包失败")
            }
        })
    },
    formatStr(str) {
        if (str.length < 10) {
            return str
        } else {
            return str.substring(0, 10) + '...'

        }
