    //提交
    submitAssessment(){
        let _this = this
       let formData  = new FormData()
       if(_this.currentColTime==""||_this.currentColTime==null){
          this.$message({
                    showClose: true,
                    message: "请选择时间批次",
                    type: 'warning'
              });
            return false
       }

      if(_this.selectData == "" || _this.selectData == null || _this.selectData == undefined){
         this.$message({
            showClose: true,
            message: "请选择项目",
            type: 'warning'
          });
        return false
      }else{
        for(let i=0; i<_this.selectData.length; i++){
          if(_this.selectData[i].auditPlanStatus == '未通过' ){
            this.$message({
              showClose: true,
              message: "不能再次提交可研评审计划编制评审退回的项目，请在待办中操作",
              type: 'warning'
            });
            return false
          }
          if(_this.selectData[i].auditPlanStatus == '已通过' || _this.selectData[i].auditPlanStatus == '已提交'){
            this.$message({
              showClose: true,
              message: "所选项目中包含已通过或者已提交状态的项目，请重新选择",
              type: 'warning'
            });
            return false
          }
        }
      }

       this.$confirm('是否提交评审计划?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
			  }).then(() => {
          // 调用流程中心
          if(this.$store.state.user.workflowtype==0) {
            let specRoleID = '';//专项角色ID
            let iscOrg = window.JSON.parse(sessionStorage.getItem("iscOrg"));//isc组织机构
            let specRoleMap = window.JSON.parse(sessionStorage.getItem("specRoleMap"));//isc专项role
            let purchaseRoleMap = window.JSON.parse(sessionStorage.getItem("purchaseRoleMap"));//isc零购专项role
            let electronicRoleMap = window.JSON.parse(sessionStorage.getItem("electronicRoleMap"));//isc电网基建专项role
            let userid = sessionStorage.getItem("userId");


            let params = {
              "user": userid,
              "orgIds": iscOrg.id,
              "roleIds": specRoleID,
              "processDefName": "plan_review",
              "activityDefID": 'KYJHBZSP',
              "processInstName": '',
              "processInstDesc": '',
              "itemId":""
            }
            for(let i in this.selectData){
              let item = this.selectData[i];
              specRoleID = specRoleMap[item.specialityT];//根据专项类型
              params.roleIds = specRoleID;
              if (item.specialityT== '01') {//如果是电网基建角色从electronicRoleMap取
                specRoleID = electronicRoleMap[item.specDetailClassT];
                params.roleIds = specRoleID
              }
              if (item.specialityT == '08') {//如果是电网基建角色从purchaseRoleMap取
                specRoleID = purchaseRoleMap[item.specClassT];
                params.roleIds = specRoleID
              }
              params.itemId = item.itemId

              params.processInstName = item.itemName + '-可研评审计划编制评审'
              params.processInstDesc = item.itemName + '-可研评审计划编制评审'

              if(params.orgIds == undefined){
                params.orgIds = "";
              }

              if(params.roleIds == undefined){
                params.roleIds = "";
              }
              //调用流程中心启动
              createAndReassignAndfinishWorkItemWithOption(params).then(res => {
                if(res.data.status == 0)
                {
                  let finishparams = {
                    processInstID: res.data.data,
                    userId: userid,
                    itemId:item.itemId,
                    type:""
                  }
                  if(item.batchGroupId != "" && item.batchGroupId != undefined && item.batchGroupId != null){
                        finishparams.isBatchPackage = true
                  }else{
                        finishparams.isBatchPackage = false
                  }
                  // 将返回值信息及项目信息插入后端数据库
                  createProcess(finishparams).then(finishres => {
                    if (finishres.data.successful) {
                      let saveWorkItemParams ={
                        processInstID:finishparams.processInstID,
                        itemId:item.itemId,
                        userId:userid,
                        isAudit:'',
                        workItemId:'123',
                        auditDesc:'评审计划编制',
                        itemPhaseDet:'7'
                      }
                      
                      if(item.batchGroupId != "" && item.batchGroupId != undefined && item.batchGroupId != null){
                        saveWorkItemParams.isBatchPackage = true
                      }else{
                        saveWorkItemParams.isBatchPackage = false
                      }
                      saveWorkItem(saveWorkItemParams).then(res=>{
                        if (finishres.data.successful) {
                          let statusParam = {
                            itemId:item.itemId,
                            isBatchPackage:saveWorkItemParams.isBatchPackage,
                            status:2
                          }
                          updateAuditPlanStatus(statusParam).then(res => {
                            if(res.data.successful){
                              this.$refs.singleTable.clearSelection()
                              this.$message.success("提交评审计划编制成功")
                              this.getTable(this.currentPage, this.currentNum,this.currentColTime.batchId)
                            }
                          })
                          
                        }
                        else {
                          let error = finishres.data.resultHint;
                          this.$message.error(item.itemName + error);
                        }
                      })
                      
                    }else {
                      let error = finishres.data.resultHint;
                      this.$message.error(item.itemName + error);
                    }
                  });
                }else {
                  this.$message.error(item.itemName + res.data.message);
                }
              })
            }	
          }
          
        })

    },
    //时间戳字符串的转化
    formartDate(param) {
      var time = new Date(param);
      var y = time.getFullYear();
      var m = time.getMonth()+1;
      var d = time.getDate();
      // var h = time.getHours();
      // var mm = time.getMinutes();
      // var s = time.getSeconds();
      // return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
      return y+'-'+this.add0(m)+'-'+this.add0(d);
    },
    add0(m){
      return m<10?'0'+m:m;
    },
