    //获取评审计划信息列表
    getColTimeList(data){
      let _this = this
      _this.timeListloading = true
      console.log(data)
      getBatchNameList(data).then(response=>{
          console.log(response)
          _this.timeListloading = false
          if(response.data.successful){
              let arr = response.data.resultValue
              _this.colTimeList = []
              for(let i=0; i<arr.length; i++){
                let tempObj = {}
                  tempObj.batchName = arr[i].batchName
                  tempObj.batchId = arr[i].batchId
                  tempObj.index = i
                  _this.colTimeList.push(tempObj)
              }
              if(arr.length>0){
                _this.currentColTime = arr[0]
                _this.$refs.tableLeft.setCurrentRow(_this.colTimeList[0],true)
                _this.queryBatch = _this.colTimeList[0].batchId
              }else{
                _this.tableDataShow = []
              }

              // if(arr != "" && arr != undefined && arr != null){
              //   _this.getTable(1,_this.currentNum,_this.currentColTime.batchId)
             
              // }
              
          }else{
            this.$message({
              showClose: true,
              message: '获取评审计划信息列表失败',
              type: 'error'
            });
          }
      })
    },
    //展示项目详细信息
    showItemInfo(data){
     let _this = this;
			_this.tempItemId = data.itemId;
			this.$router.push({path:'/itemInfo',query:{itemId:_this.tempItemId ,oldPath:_this.$route.path}});
    },
    //监听表单选中事件
    handleSelectionChange(val){
      let _this = this
      _this.selectData = []
      val.forEach(function(item){
        _this.selectData.push(item)
      })
    },
    //获取当前每页条数
    handleSizeChange(val) {
      this.currentNum = val;
      this.getTable(this.currentPage,val,this.currentColTime.batchId)
    },
    //分页查询
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val;
      this.getTable(val,this.currentNum,this.currentColTime.batchId)
    },
    tableScrollMove(refName,index){
      if(!refName || !this.$refs[refName]) return//不存在表格的ref vm 则返回
      let vmEl = this.$refs[refName].$el
      if(!vmEl) return
      //计算滚动条的位置
      const targetTop = vmEl.querySelectorAll('.el-table__body tr')[index].getBoundingClientRect().top
      const containerTop = vmEl.querySelector('.el-table__body').getBoundingClientRect().top
      const scrollParent = vmEl.querySelector('.el-table__body-wrapper')
      scrollParent.scrollTop = targetTop - containerTop
    },
    //获取数据列表
    getTable(page,size,batchId){
        let _this = this
        _this.tableDataLoading = true
        _this.loading = true
        _this.param.pageIndex = page
        _this.param.pageSize = size
        _this.param.batchId = batchId

        queryTitle().then(response => {//{}写需要的参数
          _this.tableDataLoading = false
          console.log('response',response)//后端返回数据
          if(response.data.successful){
            _this.tableLabel = response.data.resultValue
            _this.dictMap = _this.getDictArr(_this.tableLabel)
            _this.columnTypeMap = _this.getColumnTypeArr(_this.tableLabel)
            planHookUp(_this.param).then(response => {
              console.log('planHookUp',response)
              _this.loading = false;
              if(response.data.successful){
                _this.totalNum = response.data.resultValue.totalNum;
                _this.tableData = response.data.resultValue.tableData;
                _this.tableDataShow = _this.getShowDateTable(_this.tableData, _this.dictMap, _this.columnTypeMap)
                // _this.tableData.forEach(function(item){
                //   item.randomItemId = item.itemId + "-"+ Math.random()
                //   console.log(item.itemId +Math.random())
                // })
              }

            })
          }
        }).catch(error => {
          console.log(error)//error
        })   
    },
     //获取字典
    getDictArr(labelList){ 
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        if(temp.codeList!=null){
          map.set(temp.colDname,temp.codeList);
        }
      }
      return map
    },
    //获取字段类型字典
    getColumnTypeArr(labelList){
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        map.set(temp.colDname,temp.colType);
      }
      return map
    },
    //获取插入和修改的表头
    getLabelList(labelList){ 
      let _this =this
      labelList.forEach(function (item) {
        _this.$set(item,'value','');
      })
      let common_table_info = {};//数组转对象，避免类型报错
      for(let i in labelList){
        common_table_info[i] = labelList[i];
      }
      return common_table_info
    },
    //获取展示数据
    getShowDateTable(tableData,dictMap,columnTypeMap){
      let _this = this
      let arr = [];
      for(let i=0;i<tableData.length;i++){
        let tempObj = tableData[i];
        if(tempObj.batchId != "" && tempObj.batchId != undefined){
          this.isBatch = true
        }else{
          this.isBatch = false
        }
        //拷贝对象
        let copyObj = new Object();
        //遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
        for (let [key, value] of Object.entries(tempObj)) {
          // console.log(key + ':' + value);
          if(dictMap.has(key)){
            let dictValueList = dictMap.get(key);
            for(let j=0;j<dictValueList.length;j++){
              let tempObj2 = dictValueList[j];
              if(tempObj2.codeValue==value){
                copyObj[key] = tempObj2.codeName;
              }
            }
            if(!copyObj[key]){
                copyObj[key] = value;
            }
          }else if(columnTypeMap.get(key)=="date"  && value){
               copyObj[key+"Rel"] = value
               let timeStr = _this.formartDate(value)
               copyObj[key] = timeStr;
          }else{
              copyObj[key] = value;
          }
        }
        arr.push(copyObj);
      }
      return arr;
    },
    beforeUploadReport(file) {
			if (file.name) {
				let pex = file.name.split('.')[1];
				if (pex == 'doc' || pex == 'docx') {
					return true;
				} else {
					this.$message.warning('文件格式不正确,只能上传doc,docx文件!');
					return false;
				}
			} else {
				this.$message.warning('文件格式不正确,只能上传doc,docx文件!');
				return false;
			}
		},
  }
}
</script>
